function UserVerify() {
  if (!("__TMP" in localStorage)) {
    Axios.get("api/generator")
      .then((response) => {
        const item = {
          key: response.data,
          email: "",
        };
        localStorage.setItem("__TMP", JSON.stringify(item));
				let buttons = document.getElementsByClassName("buy-button");
				for (var i = 0; i < buttons.length; i++) {
					buttons[i].removeAttribute("disabled");
				}
      })
      .catch((error) => { });
  }

  function IsJsonString(key) {
    try {
      var response = JSON.parse(localStorage.getItem(key));
    } catch (e) {
      localStorage.removeItem(key);
      window.location.reload();
    }
    return response;
  }

	if ("__TMP" in localStorage) {
		let buttons = document.getElementsByClassName("buy-button");
		for (var i = 0; i < buttons.length; i++) {
			buttons[i].removeAttribute("disabled");
		}
	}

  if ("__AUTH" in localStorage) {
    const token = IsJsonString("__AUTH");
    const key = IsJsonString("__TMP");
    const now = new Date();
    if (now.getTime() > token.expiry) {
      localStorage.removeItem("__AUTH");
      window.location.reload();
    } else {
      Axios.post("api/auth/user", { token: token.value })
        .then((response) => {
          if (
            window.location.pathname.split("/")[2] == "login" ||
            window.location.pathname.split("/")[2] == "cadastro"
          ) {
            window.location.href = "";
          } else if (document.getElementById("header-button-main")) {
            document.getElementById(
              "header-button-main"
            ).children[0].innerText = "CONTA";
          }
          let item = JSON.parse(localStorage.getItem("__TMP"));
          item.email = response.data;
          localStorage.setItem("__TMP", JSON.stringify(item));
					let buttons = document.getElementsByClassName("buy-button");
					for (var i = 0; i < buttons.length; i++) {
						buttons[i].removeAttribute("disabled");
					}
        })
        .catch((e) => {
          if (e.response.data.msg == "Token Invlido") {
            localStorage.removeItem("__AUTH");
            window.location.href = "/cliente/login";
          }
        });
    }
  }
}

if (window.location.pathname.split("/")[1] != "cms") {
  window.addEventListener("storage", (e) => {
    if (e.key === "__TMP") {
      if ("__TMP" in localStorage) {
        if (
          localStorage.getItem("__TMP") === "" ||
          localStorage.getItem("__TMP") === null
        ) {
          localStorage.removeItem("__TMP");
        }
        if (location.pathname.split("/")[2] != "carrinho") {
          location.href = "";
        }
      } else {
        // location.href = "";
        UserVerify();
      }
    } else if (e.key === "__AUTH") {
      location.href = "/cliente/login";
      localStorage.removeItem("__TMP");
    } else if (!("__TMP" in localStorage)) {
      // location.href = "";
    }
  });
  UserVerify();
}

async function assignPassport() {
	await Axios.post("api/cart/attribution", {
		temp: localStorage.getItem("__TMP"),
		client_id: JSON.parse(localStorage.getItem("__AUTH")).value,
		post_attribution: false
	})
	.then((response) => {
		Swal.fire({
			title: " Item adicionado ",
			text: "Voc est sendo direcionado para o carrinho",
			showConfirmButton: false,
			closeOnClickOutside: false,
			allowOutsideClick: false,
			closeOnClickOutside: false,
		});
		location.href = "/cliente/carrinho";
	});
}

//Creates the cart on buy
window.buy = async function (id) {
  if ("__TMP" in localStorage) {
    let buttons = document.getElementsByClassName("buy-button");
    for (var i = 0; i < buttons.length; i++) {
      buttons[i].setAttribute("disabled", true);
    }
		Swal.fire({
			title: " Preparar... ",
			text: "Aguarde, estamos adicionando ao carrinho",
			showConfirmButton: false,
			closeOnClickOutside: false,
			allowOutsideClick: false,
			closeOnClickOutside: false,
		});
    Axios.post("api/cart/create", {
      temp: localStorage.getItem("__TMP"),
      ticket_id: id,
			event_id: 850
    })
    .then((response) => {
      if (!("__AUTH" in localStorage)) {
				Swal.fire({
					title: " Item adicionado ",
					text: "Voc est sendo direcionado para o carrinho",
					showConfirmButton: false,
					closeOnClickOutside: false,
					allowOutsideClick: false,
					closeOnClickOutside: false,
				});
        location.href = "/cliente/carrinho";
      }
      if (response.status == 200) {
        assignPassport();
      } else if (response.status == 202) {
        Swal.fire({
          title: "Oops :|",
          text: response.data.error,
          confirmButtonColor: "#28a745",
        });
				let buttons = document.getElementsByClassName("buy-button");
				for (var i = 0; i < buttons.length; i++) {
					buttons[i].removeAttribute("disabled");
				}
      }
    })
		.catch((error) => {
			console.log("entrei");
			Swal.fire({
				title: "Oops :|",
				text: "Tivemos um pequeno problema. Por favor, tente novamente mais tarde.",
				showCancelButton: false,
				confirmButtonText: "Ok",
				confirmButtonColor: "#6fe335",
			});
			for (var i = 0; i < buttons.length; i++) {
				buttons[i].removeAttribute("disabled");
			}
		});
  }
};

//Removes the accoutn button if the client is not logged
if (!("__AUTH" in localStorage)) {
  if (
    location.href.split("/")[4] != "login" &&
    location.href.split("/")[4] != "cadastro"
  ) {
		var submenus = document.getElementsByClassName("header__account");
		submenus[0].classList.remove("menu-active");
		submenus[1].classList.remove("menu-active");
		submenus[2].classList.remove("menu-active");
  }
} else {
	document.getElementsByClassName("header__account")[3].classList.add("menu-vanish");
}

var menu_mobile = true;
window.openMobileMenu = function () {
  if (menu_mobile) {
    document
      .getElementsByClassName("header__hamburger")[0]
      .children[0].classList.remove("fa-bars");
    document
      .getElementsByClassName("header__hamburger")[0]
      .children[0].classList.add("fa-times");
    document.getElementById("mobile-menu").classList.add("mobile-active");
    menu_mobile = false;
  } else {
    document
      .getElementsByClassName("header__hamburger")[0]
      .children[0].classList.remove("fa-times");
    document
      .getElementsByClassName("header__hamburger")[0]
      .children[0].classList.add("fa-bars");
    document.getElementById("mobile-menu").classList.remove("mobile-active");
    menu_mobile = true;
  }
};

window.changeLoginSignup = function () {
  var page = location.href.split("/")[4] == "login" ? "cadastro" : "login";
  Swal.fire({
    title: "Ir para " + page,
    text:
      "Voc tem certeza que deseja ir para o " +
      page +
      "? " +
      (page != "login"
        ? "Isso apagar seus dados dos campos e-mail e senha."
        : "Isso apagar todos os dados que voc preencheu at agora."),
    showCancelButton: true,
    cancelButtonText: "Cancelar",
    confirmButtonText: "Ir para " + page,
    cancelButtonColor: "#DF1949",
    confirmButtonColor: "#8B8B8B",
  }).then((confirm) => {
    if (confirm.value) {
      location.href = "/cliente/" + page;
    }
  });
};


window.logOut = function () {
  Swal.fire({
    title: "<span style='color:white;'>Realmente deseja sair?</span>",
    showCancelButton: true,
    confirmButtonText: `Sair`,
    cancelButtonText: `Cancelar`,
    background: "#231F20",
  }).then((result) => {
    if (result.value) {
      localStorage.clear();
      location.href = "/";
    }
  });
}